
body {
  margin: 0;
  background-color: #1E1E1E;
}
h2 {
  font-family: "Ziamimi-Bold";
  font-size: 100pt;
  margin: auto;
  position: absolute;
  left: 0;
  right: 0;
}
h2:before, h2:after {
    left: 0;
    right: 0;
    margin: auto;
}
.main h2:before, .main h2:after {
  content: attr(data-text);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.main h2:before {
  left: 2px;
  text-shadow: -1px 0 red;
  /* important: opaque background masks the original */
  background: #1E1E1E;
  clip: rect(44px, 450px, 56px, 0);
  -webkit-animation: glitch-anim-1 1s infinite linear alternate-reverse;
          animation: glitch-anim-1 1s infinite linear alternate-reverse;
}
.main h2:after {
  left: -2px;
  text-shadow: -1px 0 blue;
  /* important: opaque background masks the original */
  background: #1E1E1E;
  clip: rect(44px, 450px, 56px, 0);
  -webkit-animation: glitch-anim-2 2s infinite linear alternate-reverse;
          animation: glitch-anim-2 2s infinite linear alternate-reverse;
}
.main .top {
  background-color: #1E1E1E;
  overflow: hidden;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: solid 2px  #1E1E1E;
}
.main .top h2 {
    -webkit-animation: text-glitch 4s;
            animation: text-glitch 4s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    display: inline-block;
    color: #F1F1F1;
    bottom: -40px;
    margin-right: 22px;
}
.main .bottom {
  background-color: #1E1E1E;
  overflow: hidden;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: solid 2px  #1E1E1E;
}
.main .bottom h2 {
    -webkit-animation: text-glitch 4s;
            animation: text-glitch 4s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    display: inline-block;
    color: #F1F1F1;
    top: -10px;
    margin-left: 22px;
}
.main .bottom .projects {
    position: absolute;
    bottom: 200px;
    width: 150px;
    height: 30px;
    left: calc(50% - 75px);
    z-index: 1;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.main .bottom .projects .projectLink {
      color: #1E1E1E;
      font-family: 'Ziamimi-Regular';
      text-decoration: none;
      margin-top: 50px;
      opacity: 0;
      -webkit-transition: all 0.5s cubic-bezier(0.42, 0, 0.58, 1);
      transition: all 0.5s cubic-bezier(0.42, 0, 0.58, 1);
      -webkit-transition-delay: 0.3s;
              transition-delay: 0.3s;
}
.main .bottom .projects:before {
      content: '';
      width: 100%;
      height: 100%;
      background-color: #FFF;
      position: absolute;
      left: 0;
      top: 0;
      z-index: -1;
      -webkit-transform: scaleX(0);
              transform: scaleX(0);
      -webkit-transition: all 0.3s cubic-bezier(0.42, 0, 0.58, 1);
      transition: all 0.3s cubic-bezier(0.42, 0, 0.58, 1);
}
.main .bottom .projects:after {
      content: '';
      width: 60%;
      height: 2px;
      background-color: #1E1E1E;
      position: absolute;
      left: calc(50% - 30%);
      bottom: 3px;
      z-index: -1;
      -webkit-transform: scaleX(0);
              transform: scaleX(0);
      -webkit-transition: all 0.5s cubic-bezier(0.42, 0, 0.58, 1);
      transition: all 0.5s cubic-bezier(0.42, 0, 0.58, 1);
}
.main .bottom .projects:hover:after {
      -webkit-transform: scaleX(1);
              transform: scaleX(1);
}
.main .bottom .projects.visible .projectLink {
      margin-top: 5px;
      opacity: 1;
}
.main .bottom .projects.visible:before {
      -webkit-transform: scaleX(1);
              transform: scaleX(1);
}
.main .bottom .author {
    position: absolute;
    bottom: 25px;
    color: #FFF;
    left: calc(50% - 185px);
    opacity: 0;
    -webkit-transition: all 1s;
    transition: all 1s;
}
.main .bottom .author h1 {
      font-family: 'Ziamimi-Bold';
      margin: 0;
}
.main .bottom .author p {
      font-family: "Ziamimi-Regular";
      margin-top: 5px;
      background-color: #1E1E1E;
      position: relative;
      z-index: 1;
}
.main .bottom .author p:after, .main .bottom .author p:before {
        background-color: #FFF;
        content: '';
        display: inline-block;
        position: absolute;
}
.main .bottom .author p:after {
        left: 0;
        top: 5px;
        width: 100%;
        height: 1px;
        z-index: -2;
        -webkit-transform-origin: 50% 50%;
                transform-origin: 50% 50%;
        -webkit-transform: scaleX(0);
                transform: scaleX(0);
        -webkit-transition: all 0.5s;
        transition: all 0.5s;
        -webkit-transition-delay: 1s;
                transition-delay: 1s;
}
.main .bottom .author p:before {
        background-color: #1E1E1E;
        height: 100%;
        width: 35%;
        left: 32.5%;
        z-index: -1;
}
.main .bottom .author.visible {
      bottom: 50px;
      opacity: 1;
}
.main .bottom .author.visible p:after {
        -webkit-transform: scale(1);
                transform: scale(1);
}
@media screen and (max-width: 980px) {
.main h2 {
    font-size: 50pt;
}
.main .top h2 {
    bottom: -20px;
}
.main .bottom h2 {
    top: -5px;
}
.main .bottom .author {
    bottom: 0 !important;
    left: 0;
    right: 0;
    width: 90vw;
    margin: auto;
}
.main .bottom .author h1 {
      font-size: 20px;
}
.main .bottom .projects {
    bottom: 100px !important;
}
}
