/*
  (TEXT) PARAMS
  =================
  1. Namespace
  2. Intensity
  3. Text color
  4. Background color (flat)
  5. Highlight #1 color
  6. Highlight #2 color
  7. Width (px)
  8. Height (px)
*/
.project.ClimatePlanet {
  color: blue;
}
.project.ClimatePlanet h2 {
    color: blue;
}
.project.ClimatePlanet h2 div {
      color: blue !important;
      -webkit-transform: rotate(180deg) !important;
              transform: rotate(180deg) !important;
      display: inline-block;
}
.project.climate .logo {
  -webkit-transform: scale(0.5) !important;
          transform: scale(0.5) !important;
}
.project .top {
  background-color: #1E1E1E;
  -webkit-transition: all 0.7s cubic-bezier(0.42, 0, 0.58, 1);
  transition: all 0.7s cubic-bezier(0.42, 0, 0.58, 1);
}
.project .top h2 {
    position: relative;
    font-size: 35pt;
    width: 80%;
    margin: auto;
    letter-spacing: 30px;
    margin-right: -30px;
    left: 10%;
    margin-left: 0;
    color: #FFF;
    padding-top: 20vh;
    color: white;
    position: relative;
}
