/*
  (TEXT) PARAMS
  =================
  1. Namespace
  2. Intensity
  3. Text color
  4. Background color (flat)
  5. Highlight #1 color
  6. Highlight #2 color
  7. Width (px)
  8. Height (px)
*/
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #000;
  height: 100%;
  width: 100%;
}
#app .bruit {
    position: fixed;
    width: 100%;
    height: 100%;
    overflow: hidden;
    z-index: 10000;
    pointer-events: none;
}
#app .bruit:before {
      position: absolute;
      content: '';
      width: 500%;
      height: 500%;
      background-image: url("assets/images/background-noise.png");
      z-index: 20;
      top: -250%;
      left: -250%;
      pointer-events: none;
      -webkit-animation: grain 1s steps(10) infinite;
              animation: grain 1s steps(10) infinite;
}
#app:nth-child(1) {
    position: absolute;
    background-color: transparent;
    width: 100%;
}
#app .loader {
    position: absolute;
    left: 50%;
    top: 50%;
    z-index: 1000;
    margin-left: -27.5px;
    margin-top: -27.5px;
}
#app .loader:before {
      content: '';
      display: inline;
      width: 100vw;
      height: 100vh;
      left: 0;
      top: 0;
      position: fixed;
      background-color: #1E1E1E;
}
#app .square {
    background: white;
    width: 15px;
    height: 15px;
    float: left;
    top: -100px;
    margin-right: 5px;
    margin-top: 5px;
    position: relative;
    opacity: 0;
    -webkit-animation: enter 3s infinite;
    animation: enter 3s infinite;
}
#app .enter {
    top: 0px;
    opacity: 1;
}
#app .square:nth-child(1) {
    -webkit-animation-delay: 0.6s;
    animation-delay: 0.6s;
}
#app .square:nth-child(2) {
    -webkit-animation-delay: 0.7s;
    animation-delay: 0.7s;
}
#app .square:nth-child(3) {
    -webkit-animation-delay: 0.8s;
    animation-delay: 0.8s;
    background: #fdc96f;
}
#app .square:nth-child(4) {
    -webkit-animation-delay: 0.3s;
    animation-delay: 0.3s;
}
#app .square:nth-child(5) {
    -webkit-animation-delay: 0.4s;
    animation-delay: 0.4s;
}
#app .square:nth-child(6) {
    -webkit-animation-delay: 0.5s;
    animation-delay: 0.5s;
}
#app .square:nth-child(8) {
    -webkit-animation-delay: 0.1s;
    animation-delay: 0.1s;
}
#app .square:nth-child(9) {
    -webkit-animation-delay: 0.2s;
    animation-delay: 0.2s;
}
#app .clear {
    clear: both;
}
#app .last {
    margin-right: 0;
}
#app .landscape, #app .browser {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: #1E1E1E;
    color: #FFF;
    top: 0;
    left: 0;
    z-index: 101;
    display: none;
}
#app .landscape h2, #app .browser h2 {
      font-size: 40px;
      position: relative;
      top: calc(50% - 23px);
      color: white;
      position: relative;
}
